@keyframes slide {
  0% {
    transform: translateX(-50%);
  }

  100% {
    transform: translateX(0);
  }
}

body {
  width: 100%;
  min-height: 100vh;
  color: #fff500;
  background-color: #000431;
  justify-content: center;
  align-items: center;
  padding: 50px;
  font-family: gtfr;
  display: flex;
}

a {
  max-width: 400px;
  font-size: 1.2rem;
  text-decoration: underline;
}

.content {
  width: 100%;
  max-width: 1344px;
  border: 7px solid #fff500;
  overflow: hidden;
}

.banner {
  width: 100%;
  overflow: hidden;
}

.banner div {
  animation: slide 30s linear infinite both;
}

.banner-one div {
  width: max-content;
  overflow: hidden;
}

.banner-one div span {
  width: max-content;
  margin: 0 30px;
  font-family: gtfxebl;
  font-size: 8.5em;
  display: inline-block;
}

.banner-one div span:nth-child(2n+1) {
  -webkit-text-stroke: 2px #fff500;
  -webkit-text-fill-color: transparent;
}

.banner-two {
  border-top: 7px solid #fff500;
  border-bottom: 7px solid #fff500;
  padding: 5px 0;
  font-family: gtfxebo;
}

.banner-two div {
  width: max-content;
  animation-direction: reverse;
  overflow: hidden;
  animation-duration: 50s !important;
}

.banner-two div span {
  width: max-content;
  margin: 0 30px;
  font-size: 2.8em;
  display: inline-block;
}

.banner-two div span:nth-child(2n+1) {
  -webkit-text-stroke: 1px #fff500;
  -webkit-text-fill-color: transparent;
}

.main {
  width: 100%;
  grid-gap: 50px;
  grid-template-columns: 3fr 2fr;
  justify-content: center;
  justify-items: center;
  padding: 50px 50px 0 20px;
  display: grid;
}

.left {
  position: relative;
}

.round-large {
  width: 35vw;
  height: 35vw;
  max-width: 500px;
  max-height: 500px;
  z-index: 0;
  background-color: #fff500;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

.round-small {
  width: 2.5vw;
  height: 2.5vw;
  max-width: 42px;
  max-height: 42px;
  z-index: 10;
  background: #fff500;
  border-radius: 50%;
  position: absolute;
  top: 74%;
  left: 57%;
}

.img {
  width: 100%;
  z-index: 5;
  position: relative;
}

.right {
  grid-gap: 20px;
  grid-template-columns: 1fr;
  grid-auto-rows: min-content;
  align-content: center;
  display: grid;
}

.right p {
  text-align: left;
  width: 100%;
  max-width: 400px;
  font-size: 1.2em;
}

.contact {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.contact span {
  width: 35px;
  height: 25px;
  fill: #fff500;
  margin-right: 15px;
}

@media screen and (max-width: 1025px) {
  .main {
    padding: 20px;
  }

  .left, .right {
    grid-column: span 2;
  }

  .round-large {
    width: 45vw;
    height: 45vw;
    left: 6%;
  }

  .round-small {
    width: 4vw;
    height: 4vw;
  }
}

@media screen and (max-width: 410px) {
  .round-large {
    width: 40vw;
    height: 40vw;
    left: 6%;
  }
}

